/* eslint-disable @typescript-eslint/no-explicit-any */
import { Evento } from "./Eventos";
import { v4 as uuidv4 } from "uuid";

export interface EventCallback {
  offline: (status: boolean) => void;
}

export type EventTypes = keyof EventCallback;

export class APP {
  public isAdmin = !!localStorage.getItem("key");
  public focus = true;
  public shareable = !!navigator.share;
  private emit = {
    save: new Set() as Set<() => void>,
    onLine: new Set() as Set<(status: boolean) => void>,
    focus: new Set() as Set<(status: boolean) => void>,
  };

  public eventos = {
    values: [] as Evento[],
    add(evento: Evento) {
      const e: Evento = {
        id: evento.id || uuidv4(),
        list: evento.list || "",
        title: evento.title || "",
        locale: evento.locale || "",
        desc: evento.desc || "",
        date: evento.date ? new Date(evento.date) : undefined,
        end: evento.end ? new Date(evento.end) : undefined,
        updated: evento.updated ? new Date(evento.updated) : undefined,
        gid: evento.gid,
        recurring: evento.recurring,
      };
      this.values.push(e);
      return e;
    },
  };

  constructor(
    public title = "APP",
    public titles: string[] = [],
    public settings = {
      server: "",
      sound: false,
      notification: false,
      token: "",
    }
  ) {
    window.addEventListener("online", () => {
      this.listenerOnline(true);
    });
    window.addEventListener("offline", () => {
      this.listenerOnline(false);
    });
    window.addEventListener("focus", () => {
      this.listenerFocus(true);
    });
    window.addEventListener("blur", () => {
      this.listenerFocus(false);
    });
  }

  /**
   * Emitter
   */

  saveAllEvents() {
    this.sortEventos();
    this.emit.save.forEach((listener) => listener());
  }

  private listenerOnline(status: boolean) {
    this.emit.onLine.forEach((listener) => listener(status));
  }

  private listenerFocus(status: boolean) {
    this.focus = status;
    this.emit.focus.forEach((listener) => listener(status));
  }

  /**
   * Events
   */
  onSaveAllEvents(callback: () => void) {
    this.emit.save.add(callback);
  }

  onLine(callback: (status: boolean) => void) {
    this.emit.onLine.add(callback);
  }

  onFocus(callback: (status: boolean) => void) {
    this.emit.onLine.add(callback);
  }

  /**
   * Utils
   */
  openWindow(url: string) {
    window.open(url, "CCB", "width=1000,height=700,left=10,top=10");
  }

  shared() {
    const { hash, origin, href } = window.location;
    const url = hash.includes("#lista?filter=REGIONAL") ? origin : href;
    navigator
      .share({
        title: "Regional Faina",
        text: "Lista de atendimento e diversos ",
        url,
      })
      .then(() => console.log("Compartilhado com sucesso"))
      .catch((error) => console.error("Erro ao compartilhar:", error));
  }

  sortEventos(): Evento[] {
    if (!this.eventos.values.length) return this.eventos.values;
    this.eventos.values.sort((a, b) => {
      const titleA = this.titles.indexOf(a.title?.toUpperCase() || "");
      const titleB = this.titles.indexOf(b.title?.toUpperCase() || "");

      if (titleA !== titleB) {
        return titleA - titleB;
      }

      if (b.date && a.date && b.date.getTime() !== a.date.getTime()) {
        return a.date.getTime() - b.date.getTime();
      }

      if (a.locale && b.locale) {
        return a.locale.localeCompare(b.locale);
      }

      return 0;
    });
    return this.eventos.values;
  }
}
