import { APP } from "../../shared/app/APP";
import { titles as titlesEventos } from "../../shared/app/Titles";
import { storage } from "../../shared/utils/storage";
import { GitHubRepository } from "../../shared/utils/git";
import { Evento } from "../../shared/app/Eventos";
import { reactive } from "vue";
import { io } from "socket.io-client";

interface Message {
  room: string;
  client: string;
  msg: string;
  updated: Date;
}

export const app = <APP>reactive(new APP("APP", titlesEventos));

// Carregar dados
function load(eventos: Evento[]) {
  if (Array.isArray(eventos)) {
    app.eventos.values = [];
    eventos.map((e) => app.eventos.add(e));
    app.sortEventos();
    storage.saveItem("eventsData", app.eventos.values);
  }
}

const settings = {
  fetch: {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
    },
  },
};

fetch(`/data/events.json`, settings.fetch);
/**
 * É admin
 */
if (app.isAdmin && navigator.onLine) {
  const token = localStorage.getItem("key") || "";
  const ip_socket = "https://socket.goias.ifg.edu.br";
  const githubRepo = new GitHubRepository(
    "ccbfaina",
    "ccbfaina.github.io",
    token
  );
  const socket = io(ip_socket, {
    auth: {
      token,
    },
  });

  socket.on("connect", () => {
    console.log("Conectado no servidor socket: ", token);
    githubRepo.getContentData("data/events.json").then((res) => {
      if (res?.content) {
        socket.emit("room", <Message>{
          room: "events",
          msg: res.content,
          updated: new Date(),
        });
      }
    });
  });

  socket.on("message", (data: Message) => {
    load(JSON.parse(data.msg));
  });

  app.onSaveAllEvents(() => {
    socket.emit("room", <Message>{
      room: "events",
      msg: JSON.stringify(app.eventos.values),
      updated: new Date(),
    });
    githubRepo
      .commitFile("data/events.json", JSON.stringify(app.eventos.values))
      .then((result) => {
        if (result && result.ok) {
          console.log(
            `Dados atualizados (${app.eventos.values.length}) no Git com status ${result.status}`
          );
        }
      });
  });
} else {
  const fetchEvents = async (url: string) => {
    try {
      const response = await fetch(url);
      if (response.ok) {
        return (await response.json()) as Evento[];
      }
    } catch (error) {
      console.error(`Erro ao baixar ${url}:`, error);
    }
    return null;
  };

  const loadEvents = async (): Promise<void> => {
    try {
      const currentDateUrl = `/data/events.json?v=${new Date()
        .toISOString()
        .replace(/\D/g, "")}`;
      const currentEvents =
        (await fetchEvents(currentDateUrl)) ||
        (await fetchEvents("/data/events.json"));

      if (currentEvents) {
        load(currentEvents);
      } else {
        console.error("Erro ao carregar dados de cache.");
      }
    } catch (error) {
      console.error("Erro ao carregar os dados: ", error);
    }
  };

  loadEvents();
}

